@tailwind base;
@tailwind components;
@tailwind utilities;

/* App container */
.app-container {
  @apply min-h-screen bg-gray-50 flex flex-col items-center px-6 pt-6;
}

/* Header */
.dashboard-header {
  @apply w-full bg-white shadow-md py-6 text-center text-3xl md:text-4xl font-bold;
}

/* Main content */
.dashboard-grid {
  @apply flex flex-col items-center w-full max-w-md gap-4 mt-4;
}

/* Cards/Buttons */
.dashboard-card {
  @apply w-full bg-white rounded-xl shadow-lg px-6 py-4 text-center text-xl md:text-2xl font-medium
    text-gray-800 transition-all duration-300
    hover:shadow-2xl hover:bg-gray-100
    focus:outline-none focus:ring-2 focus:ring-blue-600 focus:ring-offset-2;
}

/* Icons for a polished look */
.dashboard-card-icon {
  @apply inline-block mr-2 text-gray-600;
}

html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
  overflow: hidden; /* Disables all scrolling */
  overscroll-behavior: none; /* Additional protection in some browsers */
}

#root {
  height: 100%;
  width: 100%;
}